import React from 'react';
import { Link } from 'gatsby';

import Dropdown from './Dropdown';
import { gotoPage } from '../../../utils/url';

import './index.scss';

type NavItemTypes = {
  url: string,
  title: string,
  list?: Omit<NavItemTypes, 'list'>[]
};

const NavItem: React.FC<NavItemTypes> = ({ url, title, list = [] }) => {
  if (list.length === 0) {
    return (
      <Link
        className="nav-btn btn btn-link"
        to={url}
        onClick={() => {
          gotoPage(url);
        }}
      >
        {title}
      </Link>
    );
  }

  return <Dropdown title={name} list={list} />;
};

export default NavItem;
