import React from 'react';

type PropTypes = {
  href: string;
  title?: string;
  target?: string;
  className?: string;
  rel?: string;
}

const ExternalLink: React.FC<PropTypes> = ({
  href, title, target = '_blank', className= '', rel = 'external',
}) => (
  <a href={href} rel={rel} target={target} className={className}>
    {title}
  </a>
);

export default ExternalLink;
